,8GAGI,cASJ,CACA,6CAKI,eAAgB,CAFhB,iBAAkB,CADlB,mCAAsC,CAItC,UAAW,CACX,gBAAiB,CACjB,iBAAkB,CAJlB,YAAa,CAKb,SAAU,CACV,iBACJ,CACA,mDACI,UAAW,CACX,WAAY,CAEZ,cAAe,CADf,YAEJ,CACA,8DAGI,+BAAgC,CADhC,UAAW,CAGX,cAAe,CAEf,eAAgB,CADhB,eAAgB,CALhB,QAAS,CAGT,YAIJ,CAIA,0HACI,YACJ,CACA,8GAOI,kBAAmB,CACnB,iBAAkB,CAFlB,UAAW,CAHX,cAAe,CADf,WAAY,CAQZ,SAAU,CANV,gBAAiB,CACjB,iBAAkB,CAIlB,iBAAkB,CAElB,KAAM,CAVN,UAWJ","sourcesContent":["<template>\n    <ul class=\"timeline\" v-if=\"!isLoading\">\n\n        <template v-for=\"(set, index) in training.training.sets\"\n                  :key=\"set.id\">\n            <li class=\"time-label\">\n                <span class=\"bg-red bg-primary text-white\">\n                    Сет №{{ index + 1 }} <span v-if=\"set.quantity\">Подходов: {{ set.quantity }}</span> <span v-if=\"set.comment\">({{ set.comment }})</span>\n                </span>\n            </li>\n\n            <li v-for=\"(activitie, index) in set.activities\">\n                <i class=\"fa bi-clock bg-blue\"></i>\n\n                <div class=\"timeline-item\">\n    <!--                <span class=\"time\"><i class=\"fa bi bi-clock\"></i> 12:05</span>-->\n\n                    <h3 class=\"timeline-header\">{{ activitie.activitie.name }}</h3>\n\n                    <div class=\"timeline-body\">\n                        <div>Повторений: {{ activitie.quantity }}</div>\n                        <div v-if=\"activitie.comment\">{{ activitie.comment }}</div>\n                        <div v-if=\"activitie.activitie.description\">{{ activitie.activitie.description }}</div>\n                    </div>\n\n                    <div class=\"timeline-footer\">\n                        <a class=\"btn btn-success btn-sm\">выполнено</a>\n                    </div>\n                </div>\n            </li>\n        </template>\n\n        <li>\n            <i class=\"fa bi bi-clock bg-gray\"></i>\n        </li>\n\n    </ul>\n    <div v-else>Тренировка загружается</div>\n</template>\n\n<script>\nimport axios from \"axios\";\n\nexport default {\n    name: \"One\",\n    props: {\n        id: {\n            type: Number,\n            required: true\n        }\n    },\n    data() {\n        return {\n            isLoading: true,\n            training: null,\n        }\n    },\n    methods: {\n        async fetch() {\n            try {\n                this.isLoading = true;\n                const response = await axios.get('/api/v1/trainings/' + this.id, {});\n\n                if (response.data.status == 'ok') {\n                    this.training = response.data.t;\n                }\n\n            } catch (e) {\n                alert('Ошибка');\n            } finally {\n                this.isLoading = false;\n            }\n        },\n    },\n    mounted() {\n        this.fetch();\n    }\n}\n</script>\n\n<style scoped>\n.timeline {\n    position: relative;\n    margin: 0 0 30px 0;\n    padding: 0;\n    list-style: none;\n}\n.timeline:before {\n    content: '';\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    width: 4px;\n    background: #ddd;\n    left: 31px;\n    margin: 0;\n    border-radius: 2px;\n}\n.timeline>li {\n    position: relative;\n    margin-right: 10px;\n    margin-bottom: 15px;\n}\n.timeline>.time-label>span {\n    font-weight: 600;\n    padding: 5px;\n    display: inline-block;\n    background-color: #fff;\n    border-radius: 4px;\n}\n.timeline>li:before, .timeline>li:after {\n    content: \" \";\n    display: table;\n}\n.timeline>li>.fa, .timeline>li>.glyphicon, .timeline>li>.ion {\n    width: 30px;\n    height: 30px;\n    font-size: 15px;\n    line-height: 30px;\n    position: absolute;\n    color: #666;\n    background: #d2d6de;\n    border-radius: 50%;\n    text-align: center;\n    left: 18px;\n    top: 0;\n}\n.timeline>li>.timeline-item {\n    -webkit-box-shadow: 0 1px 1px rgb(0 0 0 / 10%);\n    box-shadow: 0 1px 1px rgb(0 0 0 / 10%);\n    border-radius: 3px;\n    margin-top: 0;\n    background: #fff;\n    color: #444;\n    margin-left: 60px;\n    margin-right: 15px;\n    padding: 0;\n    position: relative;\n}\n.timeline>li>.timeline-item>.time {\n    color: #999;\n    float: right;\n    padding: 10px;\n    font-size: 12px;\n}\n.timeline>li>.timeline-item>.timeline-header {\n    margin: 0;\n    color: #555;\n    border-bottom: 1px solid #f4f4f4;\n    padding: 10px;\n    font-size: 16px;\n    line-height: 1.1;\n    font-weight: 600;\n}\n.timeline>li>.timeline-item>.timeline-body, .timeline>li>.timeline-item>.timeline-footer {\n    padding: 10px;\n}\n.timeline>li>.timeline-item>.timeline-body, .timeline>li>.timeline-item>.timeline-footer {\n    padding: 10px;\n}\n.timeline>li>.fa, .timeline>li>.glyphicon, .timeline>li>.ion {\n    width: 30px;\n    height: 30px;\n    font-size: 13px;\n    line-height: 30px;\n    position: absolute;\n    color: #666;\n    background: #d2d6de;\n    border-radius: 50%;\n    text-align: center;\n    left: 18px;\n    top: 0;\n}\n\n</style>\n"],"sourceRoot":""}]);
